:root {
  --next: 1;
  --previous: 0;
}

.roulette {
  position: absolute;
  bottom: -50vh;
  height: 100vh;
  width: 100vh;
  background-color: var(--color);
  -webkit-transition: -webkit-transform .5s cubic-bezier(0.39,-0.01, 0.58, 0.97);
  transition: -webkit-transform .5s cubic-bezier(0.39,-0.01, 0.58, 0.97);
  transition: transform .5s cubic-bezier(0.39,-0.01, 0.58, 0.97);
  transition: transform .5s cubic-bezier(0.39,-0.01, 0.58, 0.97), -webkit-transform .5s cubic-bezier(0.39,-0.01, 0.58, 0.97);
}

.roulette.loading {
  -webkit-animation: 
    rotate1 1s 0.5s ease-in-out forwards,
    rotate2 1s 2s ease-in-out forwards, 
    rotate3 1s 3.5s ease-in-out forwards,
    rotate4 1s 5s ease-in-out forwards, 
    rotate5 1s 6.5s ease-in-out forwards;
          animation: 
    rotate1 1s 0.5s ease-in-out forwards,
    rotate2 1s 2s ease-in-out forwards, 
    rotate3 1s 3.5s ease-in-out forwards,
    rotate4 1s 5s ease-in-out forwards, 
    rotate5 1s 6.5s ease-in-out forwards;
}

.roulette.spining {
  -webkit-animation: spin 1.2s cubic-bezier(0.39,-0.01, 0.58, 0.97) forwards;
          animation: spin 1.2s cubic-bezier(0.39,-0.01, 0.58, 0.97) forwards;
}

.roulette img {
  height: 100%;
  width: 100%;
  -webkit-filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.7));
          filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.7));
}

.roulette .container {
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
}

.roulette .container .cover {
  height: 22.7vh;
  width: 22.7vh;
  margin-top: calc(9.6vh + 22.7vh);
  -webkit-transition: margin-top 0.4s cubic-bezier(0.28, 1.07, 0.4, 0.99);
  transition: margin-top 0.4s cubic-bezier(0.28, 1.07, 0.4, 0.99);
}

.roulette .container .cover.load {
  margin-top: 9.6vh;
}

.roulette .container .cover img.shot {
  -webkit-animation: shot 0.1s ease-in;
          animation: shot 0.1s ease-in;
}

.roulette .container .cover img {
  height: 100%;
  width: 100%;
  border-radius: 11.3vh;
}

@-webkit-keyframes shot {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes shot {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes rotate1 {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
}

@keyframes rotate1 {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
}
@-webkit-keyframes rotate2 {
  0% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
  100% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
}
@keyframes rotate2 {
  0% {
    -webkit-transform: rotate(60deg);
            transform: rotate(60deg);
  }
  100% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
}
@-webkit-keyframes rotate3 {
  0% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
  100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}
@keyframes rotate3 {
  0% {
    -webkit-transform: rotate(120deg);
            transform: rotate(120deg);
  }
  100% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}
@-webkit-keyframes rotate4 {
  0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
}
@keyframes rotate4 {
  0% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
}
@-webkit-keyframes rotate5 {
  0% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
  100% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
}
@keyframes rotate5 {
  0% {
    -webkit-transform: rotate(240deg);
            transform: rotate(240deg);
  }
  100% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
}
@-webkit-keyframes rotate6 {
  0% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate6 {
  0% {
    -webkit-transform: rotate(300deg);
            transform: rotate(300deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(calc(60deg * 0));
            transform: rotate(calc(60deg * 0));
    -webkit-transform: rotate(calc(60deg * var(--previous)));
            transform: rotate(calc(60deg * var(--previous)));
  }
  100% {
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * 1));
            transform: rotate(calc(360deg * 5 + 60deg * 1));
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * var(--next)));
            transform: rotate(calc(360deg * 5 + 60deg * var(--next)));
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(calc(60deg * 0));
            transform: rotate(calc(60deg * 0));
    -webkit-transform: rotate(calc(60deg * var(--previous)));
            transform: rotate(calc(60deg * var(--previous)));
  }
  100% {
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * 1));
            transform: rotate(calc(360deg * 5 + 60deg * 1));
    -webkit-transform: rotate(calc(360deg * 5 + 60deg * var(--next)));
            transform: rotate(calc(360deg * 5 + 60deg * var(--next)));
  }
}

.actionButton {
  display: flex;
  height: 64px;
  width: 100%;
  flex-direction: row;
  align-items: center;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 0 24px;
  margin: 8px 0;
  overflow: hidden;
  color: #000000;
  background-color: transparent;
  border: none;
  font-family: 'Cabin';
  font-weight: bold;
  font-size: 20px;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  opacity: 0;
}

.actionButton:nth-child(1) {
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}
.actionButton:nth-child(2) {
  -webkit-transition: all 0.1s 0.1s ease-in-out;
  transition: all 0.1s 0.1s ease-in-out;
}

.actionButtonContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 0 24px;
  box-sizing: border-box;
}

.actionButton img {
  height: 52px;
  width: 52px;
  margin-right: 24px;
}

.actionButton:hover {
  background-color: #0000001a;
  cursor: pointer;
}

.actionButtonContainer.visible .actionButton {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.warningDialog {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 360px;
  background-color: #ffffff;
  border-radius: 20px;
  padding: 32px 64px 16px 64px;
  box-sizing: border-box;
  box-shadow: 0px 0px 20px 5px rgba(0, 0, 0, 0.32);
  z-index: 1;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 1;
  -webkit-transition: opacity 0.3s ease-out, -webkit-transform 0.3s ease-in;
  transition: opacity 0.3s ease-out, -webkit-transform 0.3s ease-in;
  transition: transform 0.3s ease-in, opacity 0.3s ease-out;
  transition: transform 0.3s ease-in, opacity 0.3s ease-out, -webkit-transform 0.3s ease-in;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.warningDialog::after {
  position: absolute;
  height: 110vh;
  width: 100vw;
  background-color: #000000aa;
  content: '';
  -webkit-transform: translateZ(-1px);
          transform: translateZ(-1px);
  -webkit-transition: opacity 0.1s ease-in-out;
  transition: opacity 0.1s ease-in-out;
}

.warningDialog.hidden::after {
  opacity: 0;
}

.warningDialog.hidden {
  -webkit-transform: translate(-50%, -40%);
          transform: translate(-50%, -40%);
  opacity: 0;
  visibility: hidden;
}

.warningDialog img {
  height: 128px;
  width: 128px;
  margin-bottom: 32px;
}

.warningDialog h2 {
  font-family: 'Cabin', sans-serif;
  font-weight: bold;
  font-size: 28px;
  margin-bottom: 16px;
}

.warningDialog p {
  font-family: 'Cabin', sans-serif;
  color: #8d8d8d;
  text-align: center;
  margin-bottom: 32px;
}

.warningDialog .retry {
  width: 100%;
  background-color: #ff6c2c;
  color: #ffffff;
  border: none;
  font-family: 'Cabin', sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  box-sizing: border-box;
  padding: 12px 32px;
  border-radius: 24px;
}

.warningDialog .retry:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
  -webkit-transition: all 0.1 ease-in;
  transition: all 0.1 ease-in;
}

.warningDialog .continue {
  background-color: transparent;
  border: none;
  font-family: 'Cabin', sans-serif;
  font-size: 16px;
